/*
*** Colors ***
*/
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-VariableFont_slnt,wght.ttf");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
figure,
blockquote,
dl,
dd {
  padding: 0;
  margin: 0;
}

body.is-locked {
  overscroll-behavior: none;
}

html {
  scroll-behavior: smooth;
}

button {
  padding: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}

::selection {
  background-color: #C4132F;
  color: #ffffff;
}

@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:focus {
  outline: 1px solid currentColor;
  outline-offset: 2px;
}
:focus:not(:focus-visible) {
  outline: none;
}

.lazyloaded {
  transition: opacity 300ms ease-in-out;
}

.lazyload,
.lazyloading {
  opacity: 0;
}

.color-dark {
  color: #181818;
}

.color-light {
  color: #ffffff;
}

.color-primary {
  color: #C4132F;
}

body,
input,
button,
select,
textarea {
  font-family: Inter, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #181818;
  font-weight: 500;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: 700;
}

a {
  color: #C4132F;
  text-decoration: none;
}

p {
  margin-bottom: 1em;
  margin-top: 0;
}
p:last-child {
  margin-bottom: 0;
}

