.button {
  display: inline-block;
  align-items: center;
  text-align: center;
  cursor: pointer;
  border: none;
  position: relative;
  transition: 100ms cubic-bezier(0.42, 0, 0.58, 1);
  text-transform: uppercase;
  letter-spacing: 0.08em;
}
.button > .preloader {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateZ(0);
  right: 20px;
}
.button--style--primary {
  background-color: #C4132F;
  border: 1px solid transparent;
  color: #ffffff;
}
.no-touch .button--style--primary:hover {
  background-color: rgb(149.5069767442, 14.4930232558, 35.8511627907);
}
.no-touch .button--style--primary:active {
  background-color: rgb(103.0139534884, 9.9860465116, 24.7023255814);
}
.button--style--primary.disabled, .button--style--primary[disabled] {
  background-color: #F9F9F9;
  border-color: #F9F9F9;
  color: #E6E6E6;
  pointer-events: none;
}
.button--style--primary-white {
  background-color: #ffffff;
  border: 1px solid transparent;
  color: #C4132F;
}
.no-touch .button--style--primary-white:hover {
  background-color: rgba(255, 255, 255, 0.8);
}
.no-touch .button--style--primary-white:active {
  background-color: rgba(255, 255, 255, 0.5);
  color: rgb(103.0139534884, 9.9860465116, 24.7023255814);
}
.button--style--primary-white.disabled, .button--style--primary-white[disabled] {
  background-color: rgba(255, 255, 255, 0.1);
  border-color: transparent;
  color: rgba(255, 255, 255, 0.2);
  pointer-events: none;
}
.button--style--link, .button--style--link-white {
  padding: 0 !important;
}
.no-touch .button--style--link:hover .button__label, .no-touch .button--style--link:active .button__label, .no-touch .button--style--link-white:hover .button__label, .no-touch .button--style--link-white:active .button__label {
  border-color: currentColor;
}
.button--style--link.disabled, .button--style--link[disabled], .button--style--link-white.disabled, .button--style--link-white[disabled] {
  color: #999999;
  pointer-events: none;
}
.button--style--link .button__label, .button--style--link-white .button__label {
  border-bottom: 1px solid transparent;
  transition: border 100ms cubic-bezier(0.42, 0, 0.58, 1);
}
.button--style--link {
  color: #C4132F;
}
.no-touch .button--style--link:hover {
  color: rgb(149.5069767442, 14.4930232558, 35.8511627907);
}
.no-touch .button--style--link:active {
  color: rgb(103.0139534884, 9.9860465116, 24.7023255814);
}
.button--style--link-white {
  color: #ffffff;
}
.no-touch .button--style--link-white:active {
  color: #B3B3B3;
}
.button--style--hover-red {
  background-color: transparent;
  color: #ffffff;
  border: 1px solid #ffffff;
  font-size: 18px;
}
.no-touch .button--style--hover-red:hover, .no-touch .button--style--hover-red:focus {
  background-color: red;
  color: #ffffff;
  border-color: red;
}
.button--size--default {
  font-size: 16px;
