.overlay {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  z-index: 120;
}
@media (min-width: 47.5em) {
  .overlay {
    padding: 80px;
  }
}
.overlay > .preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) translateZ(0);
}
.overlay__content {
  animation: showOverlayContent 0.4s forwards;
  background-color: white;
  width: 100%;
  box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.08);
  border-radius: 8px;
  position: relative;
}
.overlay__content--default {
  max-width: 900px;
}
.overlay__content--full-hd {
  max-width: 1920px;
}
.overlay__content--transparent {
  box-shadow: none;
  background-color: transparent;
}
.overlay__content--cookie-settings {
  max-width: 640px;
}
.overlay__content--cookie-settings-large {
  max-width: 1424px;
}
.is-removed .overlay__content {
  animation: hideOverlayContent 0.4s forwards;
}
.overlay__content:focus {
  outline: none;
}
.overlay__close {
  transition: color 0.2s;
  position: absolute;
  top: 0;
  right: 0;
  padding: 1em;
  font-size: 16px;
  cursor: pointer;
  z-index: 10;
}
.no-touch .overlay__close:hover {
  color: #C4132F;
}
.overlay__close svg {
  pointer-events: none;
}

@keyframes showOverlayContent {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
@keyframes hideOverlayContent {
  from {
    opacity: 1;
    transform: none;
  }
  to {
    opacity: 0;
    transform: translateY(5px);
  }
}
.page::after {
  content: "";
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.3);
  transition: opacity, visibility, z-index;
  transition-delay: 0s, 0s, 0.5s;
  transition-duration: 0.5s, 0.5s, 0.01s;
  pointer-events: none;
}
.is-dimmed .page::after {
  visibility: visible;
  pointer-events: auto;
  opacity: 1;
}
.is-locked .page {
  position: fixed;
  left: 0;
  width: 100%;
}

.preloader {
  color: #C4132F;
  font-size: 14px;
}
.preloader--white {
  color: #fff;
}
.preloader--black {
  color: #181818;
}
.preloader--big {
  font-size: 24px;
}
.preloader__box {
  width: 1em;
  height: 1em;
  animation: clockwise 500ms linear infinite;
}
.preloader__cut {
  position: relative;
  width: 0.5em;
  height: 1em;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}
.preloader__donut {
  box-sizing: border-box;
  width: 1em;
  height: 1em;
  border: 2px solid currentColor;
  border-radius: 50%;
  border-left-color: transparent;
  border-bottom-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  background: none;
  margin: 0;
  animation: donut-rotate 1000ms cubic-bezier(0.4, 0, 0.22, 1) infinite;
}

@keyframes clockwise {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes donut-rotate {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(-140deg);
  }
  100% {
    transform: rotate(0);
  }
}
.teaser {
  position: relative;
  padding: 35px 27px;
}
.teaser:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #E5EAEE;
}
.teaser--style--black {
  color: #ffffff;
}
.teaser--style--black:before {
  background: #181818;
}
.teaser > * {
  position: relative;
  z-index: 1;
}
.teaser__illustration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}
.teaser__uptitle {
  margin-bottom: 1.5rem;
}
.teaser__headline, .teaser__subtitle {
  margin-bottom: 0.5rem;
}

.hero {
  overflow: hidden;
}
.hero__main {
  position: relative;
  z-index: 2;
  min-height: 80vh;
  padding: 80px 0;
  display: flex;
  align-items: center;
}
@media (min-width: 47.5em) {
  .hero__main {
    padding: 120px 0;
  }
}
.hero__visual {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.hero__visual:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.66) 6.22%, rgba(255, 255, 255, 0) 51.23%);
}
.hero__visual__image {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}
.hero__inner {
  position: relative;
}
.hero__inner__content {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  column-gap: 8px;
}
@media (min-width: 47.5em) {
  .hero__inner__content {
    grid-template-columns: repeat(8, minmax(0, 1fr));
  }
}
@media (min-width: 61.25em) {
  .hero__inner__content {
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }
}
.hero__headline {
  grid-column: 1/span 7;
}
@media (min-width: 61.25em) {
  .hero__headline {
    grid-column: 1/span 6;
  }
}
.hero__text {
  grid-column: 1/span 6;
}
@media (min-width: 61.25em) {
  .hero__text {
    grid-column: 1/span 4;
  }
}
.hero__teasers {
  margin: 0 -20px;
}
@media (min-width: 47.5em) {
  .hero__teasers {
    margin: 0 -80px;
  }
}
@media (min-width: 61.25em) {
  .hero__teasers {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 61.25em) {
  .hero__teasers .teaser {
    padding-top: 186px;
    margin-top: -120px;
  }
}
.hero__teasers .teaser:first-child:before {
  left: -30vw;
}
.hero__teasers .teaser:last-child {
  grid-column: span 2;
}

.navigation {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  border-bottom: 1px solid #ffffff;
  transition: background 300ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
@media (max-width: 61.24em) {
  .navigation.is-mobile-navigation-open {
    position: fixed;
    background: #181818;
  }
}
.navigation__inner {
  --navigation-height: 70px;
  display: flex;
  align-items: center;
  height: var(--navigation-height, 80px);
  gap: 1.5rem;
}
@media (min-width: 61.25em) {
  .navigation__inner {
    --navigation-height: 95px;
    margin-right: -80px;
  }
}
.navigation__logo {
  height: 30px;
}
@media (min-width: 61.25em) {
  .navigation__logo {
    height: 45px;
  }
}
.navigation__logo__image {
  height: 100%;
}
.navigation__main {
  display: flex;
  margin-left: auto;
  height: 100%;
}
@media (max-width: 61.24em) {
  .navigation__main {
    position: fixed;
    left: 0;
    top: var(--navigation-height);
    width: 100vw;
    height: calc(calc(var(--vh, 1vh) * 100) - var(--navigation-height));
    background: #C4132F;
    transform: translateX(100%);
    flex-direction: column;
    transition: transform 300ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
  .is-mobile-navigation-open .navigation__main {
    transform: none;
  }
}
.navigation__links {
  list-style-type: none;
  padding: 0;
  margin: 0 2.5rem 0 0;
  display: flex;
  align-items: center;
  gap: 52px;
}
@media (max-width: 61.24em) {
  .navigation__links {
    flex-direction: column;
    padding: 4rem 1rem;
    margin: 0;
  }
}
.navigation__links__item {
  opacity: 0.5;
  transition: 100ms cubic-bezier(0.42, 0, 0.58, 1);
}
@media (max-width: 61.24em) {
  .navigation__links__item {
    opacity: 1;
  }
}
.no-touch .navigation__links__item:hover {
  opacity: 1;
}
@media (max-width: 61.24em) {
  .navigation__button {
    width: 100%;
    margin-top: auto;
  }
}
@media (min-width: 61.25em) {
  .navigation__button {
    display: inline-flex;
    height: 100%;
  }
}
.navigation__mobile-toggle {
  position: relative;
  background: #C4132F;
  height: var(--navigation-height, 80px);
  width: var(--navigation-height, 80px);
  margin-left: auto;
  margin-right: -20px;
  cursor: pointer;
}
@media (min-width: 47.5em) {
  .navigation__mobile-toggle {
    margin-right: -80px;
  }
}
@media (min-width: 61.25em) {
  .navigation__mobile-toggle {
    display: none;
  }
}
.navigation__mobile-toggle span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) translateZ(0);
  display: block;
  width: 30px;
  height: 2px;
  background: #ffffff;
  transition: all 300ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.navigation__mobile-toggle span:first-child {
  margin-top: -8px;
}
.is-mobile-navigation-open .navigation__mobile-toggle span:first-child {
  transform: translate(-50%, -50%) rotate(45deg);
  margin: 0;
}
.is-mobile-navigation-open .navigation__mobile-toggle span:nth-child(2) {
  opacity: 0;
}
.navigation__mobile-toggle span:last-child {
  margin-top: 8px;
}
.is-mobile-navigation-open .navigation__mobile-toggle span:last-child {
  transform: translate(-50%, -50%) rotate(-45deg);
  margin: 0;
}

.text-content {
  margin: 100px 0;
}
@media (min-width: 47.5em) {
  .text-content {
    margin: 120px 0;
  }
}
@media (min-width: 73.75em) {
  .text-content {
    margin: 180px 0;
  }
}
.text-content__inner {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  column-gap: 8px;
}
@media (min-width: 47.5em) {
  .text-content__inner {
    grid-template-columns: repeat(8, minmax(0, 1fr));
  }
}
@media (min-width: 61.25em) {
  .text-content__inner {
    grid-template-columns: repeat(12, minmax(0, 1fr));
  }
}
.text-content__top {
  grid-column: 1/-1;
}
@media (min-width: 47.5em) {
  .text-content__top {
    grid-column: 1/span 3;
  }
}
@media (min-width: 61.25em) {
  .text-content__top {
    grid-column: 1/span 4;
  }
}
.text-content__headline {
  margin-bottom: 1rem;
}
.text-content__text {
  grid-column: 1/-1;
  margin-top: 1rem;
}
@media (min-width: 47.5em) {
  .text-content__text {
    grid-column: 5/span 4;
    margin-top: 0;
  }
}
@media (min-width: 61.25em) {
  .text-content__text {
    grid-column: 5/span 8;
    column-count: 2;
    column-gap: 2.5rem;
  }
